// import constructors from './constructors';

// const URL = 'http://localhost:3000/admin/api'
const domain = 'http://localhost';
const port = '3000';
const route = 'admin/api';
const queryDocs = require('./constructors')
const GRAPHQL_URL = 'http://localhost:3000/admin/api'

const createCall = async (queryDoc, cb) => {
  const payload = JSON.stringify({
    query: queryDoc
  });
  let response; 
  try {
    response = await fetch(
      GRAPHQL_URL,
      {
        method: 'POST',
        headers: {
          // Authorization: `token ${accessToken}`,
          'Content-Type': 'application/json',
        },
        body: payload,
      }
    );
  } catch(e) {
    return {
      message: e.message,
      ok: false,
      body: {}
    }
  }
  let body
  try {
    body = await response.json();
  } catch(e) {
    return {
      message: e.message,
      ok: response.ok,
      body: {}
    }
  }
  if(!response.ok) {
    return {
      status: response.status,
      ok: response.ok,
      body: {}
    }
  }
  return body.data
  ?{
    status: response.status,
    ok: response.ok,
    body: body.data
  }
  :{
    status: response.status,
    ok: false,
    body: {}
  }
}

export const queryid = (query) => {
  return createCall(queryDocs.queryAllUsers(query))
}